import React, { Component } from 'react';
import PubSub from 'pubsub-js';
import Search from '../Search';
import Upload from '../UploadFile';
import CreateFEMDEMFEM from '../CreateFEMDEMFEM';
import CreateLaminierSimulation from '../CreateLaminierSimulation';
import styles from './index.module.css';

class Content extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeKey: 0,
    };
    this.onGetActive = this.onGetActive.bind(this); //  this
  }

  componentDidMount() {
    const { state } = this;
    state.pubsub = PubSub.subscribe('getKey', (_, key) => this.onGetActive(key)); //  PubSub 
    this.setState(state);
    PubSub.subscribe('getKey', (_, key) => this.onGetActive(key)); //  PubSub 
  }

  onGetActive(key) {
    const { state } = this;
    state.activeKey = key;
    this.setState(state);
  }

  render() {
    const { activeKey } = this.state; //  state  activeKey
    const titleMap = ['SparqlQuery', 'Hochladen', 'FEM-DEM-FEM Simulation', 'Laminiersimulation']; // 
    const Title = titleMap[activeKey]; // 
    const ContentComponent = [Search, Upload, CreateFEMDEMFEM, CreateLaminierSimulation][activeKey]; // 
    return (
      <div className={styles.wrapper}>
        <div className={styles.container}>
          <h2 className={styles.title}>
            <span>{Title}</span>
          </h2>
          <ContentComponent />
        </div>
      </div>
    );
  }
}

export default Content;
